.wrapper {
  position: relative;
  // overflow: hidden;
  // display: flex;
  // flex-direction: column;
  // min-height: 100vh;
  // /* stylelint-disable-next-line */
  // min-height: calc(100 * var(--vh, 1vh));
  // align-items: center;

  &.active::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-black;
    opacity: 0.5;
    z-index: 5;
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    width: 100%;
    flex-grow: 1;
  }
}

body.active {
  overflow: hidden;
}

.rouble {
  font-family: $ff-rouble;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
