.intro {
  position: relative;
  min-height: 800px;
  height: 100vh;
  transition: 1s ease;
}
.intro__background {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: #f1e7d4;
  z-index: -1;
}
.intro__background::before {
  display: block;
  content: "";
  width: 68%;
  height: 100%;
  clip-path: polygon(0 0, 100% 0, 86.8% 100%, 0 100%);
  overflow: hidden;
  background-color: #f1e7d4;
  transition: 1s ease;
}
.intro__background::after {
  display: block;
  content: "";
  width: 32%;
  height: 100%;
  clip-path: polygon(13.2% 0, 100% 0, 100% 100%, 0 100%);
  overflow: hidden;
  background-color: #fc0000;
}
.intro--red {
  background-color: #fc0000;
}
.intro--red .intro__background::before {
  background-color: transparent;
}
.intro__content {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
}
.intro__wrapper {
  position: relative;
  display: flex;
  width: 100%;
  margin: auto 0;
}
.intro__inner {
  max-width: 800px;
}
.intro__img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -40px;
  z-index: -1;
}
.intro__description {
  font-style: italic;
  font-weight: 400;
  font-size: 30px;
  color: #fc0000;
}
.intro__title {
  font-family: "SovietFont", "Tahoma", sans-serif;
  font-weight: 400;
  font-size: 100px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #192632;
  text-transform: uppercase;
  max-width: 800px;
  margin-bottom: 50px;
  margin-left: -10px;
}
@media (max-width: 1439px) {
  .intro {
    height: auto;
  }
  .intro__content {
    flex-direction: column;
  }
  .intro__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 120px auto 0;
  }
  .intro__img {
    position: static;
    margin-top: 40px;
    margin-right: 150px;
    transform: none;
  }
}
@media (max-width: 767px) {
  .intro__inner {
    margin-top: 100px;
  }
  .intro__description {
    font-weight: 400;
    font-size: 20px;
    margin: 10px;
    text-align: center;
  }
  .intro__title {
    font-size: 40px;
    line-height: 150%;
    margin-bottom: 30px;
    text-align: center;
  }
  .intro__img {
    margin-right: 100px;
  }
  .intro__background {
    flex-direction: column;
  }
  .intro__background::before {
    width: 100%;
    height: 75%;
  }
  .intro__background::after {
    clip-path: polygon(0 0, 100% 23%, 100% 100%, 0 100%);
    width: 100%;
    height: 25%;
  }
}
@media (max-width: 500px) {
  .intro__img {
    margin-right: 0;
  }
}

.demo {
