  left: -430px;
}
.demo__string--video .demo__string-item span {
  width: 430px;
}
.demo__string--video .demo__string-item--reverse {
  left: auto;
  right: -4300px;
}
.demo__string-item {
  position: relative;
  display: flex;
  align-items: center;
  white-space: nowrap;
  left: -750px;
  height: 100px;
}
.demo__string-item--reverse {
  left: auto;
  right: -7000px;
}
.demo__string-item span {
  position: absolute;
  width: 750px;
  font-style: italic;
  font-weight: 700;
  font-size: 100px;
  line-height: 83%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
.demo__string-item span.white {
  color: #f1f1f1;
}
.demo__string-item span.transparent {
  color: transparent;
  -webkit-text-stroke: 1px #f1f1f1;
  text-stroke: 1px #f1f1f1;
  -webkit-background-clip: text;
  background-clip: text;
}
.demo__inner-label {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  bottom: 0;
  transform: translateX(50%) translateY(50%);
