  width: 135px;
  height: 135px;
  cursor: pointer;
}
.demo__btn svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s ease;
}
.demo__btn svg path,
.demo__btn svg circle {
  transition: 0.3s ease;
}
.demo__btn:hover svg path {
  fill: #fc0000;
}
.demo__btn:hover svg circle {
  stroke: #fc0000;
}
.demo__btn:active svg path {
  fill: #b90303;
}
.demo__btn:active svg circle {
  stroke: #b90303;
}
.demo__btn--link:hover svg:last-child path {
  fill: inherit;
  stroke: #fc0000;
}
.demo__btn--link:active svg:last-child path {
  fill: inherit;
  stroke: #b90303;
}
@keyframes circle {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.demo__btn .demo__circle--anim {
  top: 0;
  left: 0;
  animation: circle 3s linear infinite;
